.no-left-gutter {
  padding-left: 0px; }

.no-right-gutter {
  padding-right: 0px; }

html {
  height: 100%; }

body {
  background: -webkit-linear-gradient(#8ddca3, #55cc00);
  background: linear-gradient(#8ddca3, #55cc00);
  color: white;
  font-family: 'Open Sans';
  min-height: 100%;
  padding-bottom: 150px; }

body.landing {
  background: #5b5579;
  padding-bottom: 0; }

.player-header-nav {
  background-color: rgba(123, 70, 163, 0.9); }

body.landing .navbar {
  margin-bottom: 0; }
